body {
    font-family: sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.sidebar {
    z-index: 100;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 90px;
    height: 100%;
    background-color: white;
    transition: width 0.5s;
}

.sidebar>* {
    margin-bottom: 12vh;
}

.logoBox {
    margin-bottom: 7vh;
}

.menuOpen {
    width: 250px;
}